/**
 *    
 * @module shamUI/widget
 * @see Widget
 */
define( 'widget',[
    "lodash",
    "Class"
], function( _,  Class ) {
    var Widget = Class(
        /** @lends Widget.prototype */
        {
            /**
             * @constructs
             * @classdesc    
             * @param {String}  containerSelector CSS- ,   
             *                                     
             * @param {String}  ID                 
             * @param {Object} [options]          
             */
            constructor: function( containerSelector, ID, options ) {
                this.ID = ID;
                this.containerSelector = containerSelector;
                this.options = options || {};
                this.UI.render.register( this )
            },

            /**
             * -  
             * @member {Node}
             */
            container: null,

            /**
             *  
             * @member {Array}
             */
            types: [],
            /**
             *  -
             * @member {Object}
             * @namespace
             */
            defaultOptions: {

                /**
                 *    ,  
                 * @member {Array}
                 * @default [ "bindEvents", "render" ]
                 */
                actionSequence: [ "bindEvents", "render" ],

                /**
                 *      ,     
                 *   
                 * @member {Boolean}
                 * @default true
                 */
                bindOnce: true,

                /**
                 *       
                 * @member {Boolean}
                 * @default false
                 */
                cacheParentContainer: false,

                /**
                 *   
                 * @member {Boolean}
                 * @default false,
                 */
                renderAsync: false,

                /**
                 *    
                 * @param {Function} renderCallback callback  
                 */
                renderAsyncWrapper: function( renderCallback ) {
                    window.requestAnimationFrame( renderCallback );
                },

                /**
                 *  ,     ,    
                 * @member {Array}
                 */
                renderDependence: []
            },
            $ready: function( clazz, parent, api ) {
                var i;
                if ( this !== clazz ) {

                    //   -
                    for ( i = 0; i < parent.length; i++ ) {
                        clazz.prototype.defaultOptions = _.defaultsDeep(
                            clazz.prototype.defaultOptions,
                            parent[ i ].prototype.defaultOptions
                        );
                    }
                }
            },
            /**
             *  .   -
             * @name Widget#options
             * @type Object
             * @see Widget#defaultOptions
             */
            options: {
                get: function() {
                    return this._options;
                },
                set: function( options ) {

                    //   -
                    this._options = _.defaultsDeep( options || {}, this.defaultOptions );
                }
            },
            /**
             *   
             * @type {Function|null}
             */
            bindEvents: null,
            /**
             *    
             * @returns {{container: *, html: *}}
             */
            render: function() {
                if ( this.html ) {
                    if ( this.options.cacheParentContainer ) {
                        if ( !this.containerParentNode ) {
                            this.containerParentNode = document.querySelector(
                                this.containerSelector
                            ).parentNode;
                        }
                        this.container = this.containerParentNode.querySelector(
                            this.containerSelector
                        );
                    } else {
                        this.container = document.querySelector( this.containerSelector );
                    }
                    return {
                        container: this.container,
                        html: this.html()
                    }
                }
            },
            /**
             *   html  
             * @type {Function|null}
             */
            html: null,
            /**
             *     
             * @type {Function|null}
             */
            destroy: null
        }
    );
    /**
     *   
     * @see Widget
     */
    return Widget;
} );

